class Util {

  static generateRandomColor() {
    const r = Math.floor(Math.random() * 255);
    const g = Math.floor(Math.random() * 255);
    const b = Math.floor(Math.random() * 255);
    return "rgb(" + r + "," + g + "," + b + ")";
  }

  static selectErrorMessage(errorMessage) {
    switch (errorMessage) {
      case "name already exists":
        return "Antamasi kyttjnimi on varattu";
      case "username invalid":
        return "Kyttjnimen tytyy sislt 4-16 kirjainta ja/tai numeroa";
      case "password invalid":
        return "Salasanan tytyy sislt vhintn 8 merkki, sek vhintn yksi iso kirjain ja numero";
      case "not matching":
        return "Salasanat eivt tsm";
      case "user doesn't exist":
        return "Tili ei lytynyt";
      case "wrong password":
        return "Vr salasana";
    }
  }

  static selectAlertMessage(snackbarOpen) {
    switch (snackbarOpen) {
      case "signup":
        return "Tilin luonti onnistui!"
      case "login":
        return "Kirjautuminen onnistui!"
      case "logout":
        return "Uloskirjautuminen onnistui!"
      case "deleteuser":
        return "Kyttjn poisto onnistui!"
      case "deleteview":
        return "Nkymn poisto onnistui!"
      case "postview":
        return "Nkymn luonti onnistui!"
    }
  }
}

export default Util;